@import url(https://fonts.googleapis.com/css2?family=Cinzel+Decorative&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Cinzel+Decorative&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
body{
    background-color: #FFFBFB;
}

#Head{
    height: auto;
    display: flex;
    background-color: #FAF2DA;
    padding: 0;
    margin-top: 0;
}

.container-judul{
    margin-top: 8%;
    text-align: center;
    margin-right: 10%;
}
#Judul{
    font-size: 3.75vw;
    font-family: 'Cinzel Decorative', cursive;
    color: #4A503D;
}
#Cari{
    width: 35vw;
    height: auto;
    max-height:5vh;
    border-radius: 20px;
    border-color: #4A503D;
    background: transparent;
    font-family: 'Nunito Sans', sans-serif;
    color: #4A503D;
    font-size: 2vw;
    
}
#GambarPalm1{
    width: 16.5vw;
    height: 14.275vw;
    -webkit-transform: rotateX(180deg);
            transform: rotateX(180deg);
}

#GambarPalm2{
    right: 2%;
    margin-top: 20%;
    width: 16.5vw;
    height: 14.275vw;
}

#Cart{
    margin-top: 1%;
    width: 3vw;
    height: 3vw;
}

#GambarPalm3{
    right: 2%;
    margin-top: 20%;
    width: 16.5vw;
    height: 14.275vw;
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
}
.Badan{
    background-color: #FFFBFB;
    height: 20rem;
    text-align: center;
    color: #4A503D;
}

.JudulDagangan{
    font-size: 3vw;
    font-family: 'Nunito Sans', sans-serif;
    
}
#Item-Kresek{
    border: 0.5px solid;
    border-color: #4A503D;
    border-radius: 10px;
    width: 15.625rem;
    height: 20rem;
    background-color: #FAF2DA;
    font-family: 'Cinzel Decorative', cursive;
    font-size: 1.5rem;
    color: #4A503D;

    
    
}


.GambarProduk{
    margin: 0;
    width: inherit;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    height: auto;
    border-bottom-right-radius: 30px;
    border-bottom-left-radius: 30px;
}

#TombolBeli{
    background-color: #E28F83;
    color: #FFFBFB;
    width: 8.5rem;
    height: 1.5rem;
    border: transparent;
    border-radius: 20px;
    text-align: center;
}

#stok{
    font-size: 1rem;
    font-family: 'Nunito Sans', sans-serif;
    color: #4A503D;
}

@media only screen and (max-width: 768px) {
    #Item-Kresek{
        width: 6rem;
        height: 10rem;
        font-size: 1rem;
    }
    .GambarProduk{
        border-bottom-left-radius: 15px;
        border-bottom-right-radius: 15px;
    }
    #TombolBeli{
        background-color: #E28F83;
        color: #FFFBFB;
        width: 4rem;
        height: 1.25rem;
        border-radius: 20px;
    }
    
    #stok{
        font-size: 0.75rem;
        font-family: 'Nunito Sans', sans-serif;
        color: #4A503D;
    }
    
}

.ItemKresek{
    display: grid;
    grid-template-columns: repeat(auto-fit, 16.68rem);
    justify-content: center;
    grid-gap: 0.5rem;
    gap: 0.5rem;
    text-align: center;
    margin-left: 20px;
    
}

.Items{
    display: inline-block;
    
}

@media only screen and (max-width: 768px) {
    .ItemKresek{
        display: grid;
        grid-template-columns: repeat(auto-fit, 7rem);
        justify-content: center;
        grid-gap: 0.5rem;
        gap: 0.5rem;
        text-align: center;
        margin-left: 20px;
        
    }
}

* {
box-sizing: border-box;
}

html {
font-family: 'Noto Sans JP', sans-serif;
}

body {
margin: 0;
background-color: #FFFBFB;
}

