.ItemKresek{
    display: grid;
    grid-template-columns: repeat(auto-fit, 16.68rem);
    justify-content: center;
    gap: 0.5rem;
    text-align: center;
    margin-left: 20px;
    
}

.Items{
    display: inline-block;
    
}

@media only screen and (max-width: 768px) {
    .ItemKresek{
        display: grid;
        grid-template-columns: repeat(auto-fit, 7rem);
        justify-content: center;
        gap: 0.5rem;
        text-align: center;
        margin-left: 20px;
        
    }
}
