@import url('https://fonts.googleapis.com/css2?family=Cinzel+Decorative&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@300&display=swap');

body{
    background-color: #FFFBFB;
}

#Head{
    height: auto;
    display: flex;
    background-color: #FAF2DA;
    padding: 0;
    margin-top: 0;
}

.container-judul{
    margin-top: 8%;
    text-align: center;
    margin-right: 10%;
}
#Judul{
    font-size: 3.75vw;
    font-family: 'Cinzel Decorative', cursive;
    color: #4A503D;
}
#Cari{
    width: 35vw;
    height: auto;
    max-height:5vh;
    border-radius: 20px;
    border-color: #4A503D;
    background: transparent;
    font-family: 'Nunito Sans', sans-serif;
    color: #4A503D;
    font-size: 2vw;
    
}
#GambarPalm1{
    width: 16.5vw;
    height: 14.275vw;
    transform: rotateX(180deg);
}

#GambarPalm2{
    right: 2%;
    margin-top: 20%;
    width: 16.5vw;
    height: 14.275vw;
}

#Cart{
    margin-top: 1%;
    width: 3vw;
    height: 3vw;
}

#GambarPalm3{
    right: 2%;
    margin-top: 20%;
    width: 16.5vw;
    height: 14.275vw;
    transform: rotateY(180deg);
}
.Badan{
    background-color: #FFFBFB;
    height: 20rem;
    text-align: center;
    color: #4A503D;
}

.JudulDagangan{
    font-size: 3vw;
    font-family: 'Nunito Sans', sans-serif;
    
}